*{
    margin: 0;
    padding: 0;
}

body{
    height: 100vh;
    background-image: linear-gradient(to bottom right, #49d5d1, #202b98);
}

#submit-registration{
    color: #202b98;
    font-size: 12px;
    border-radius: 15%;
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.15);
    height: 2rem;
    width: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #49d5d1;
    cursor: pointer;
   text-decoration: none;
}

#register-button:hover{
    opacity: 0.7;   
}

.user-info{
    position: relative;
    bottom: 1rem;
}

#register-box{
    background-color: whitesmoke;
    height: 75vh;
    width: 50%;
    position: absolute;
    top: 23vh;
    right: 25%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 2%;
}

#join-text{
    color:whitesmoke;
    font-size: 10rem;
    font-weight: bold;
    font-style: normal;
    position: relative;
    bottom: 2rem;
}

#view{
    display:flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
}